import React, { useState } from "react"
import { Link } from "gatsby"
import { Row, Col } from 'antd';
import { Menu, Icon, Button, Layout } from 'antd';


const { SubMenu } = Menu;
const { Footer, Sider, Content } = Layout;
const LayoutPage = (props) => {
  const [collapsed, setCollapsed] = useState(true)
  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };
  return (
  <>
    <Layout>
      <Row>
        <Col style={{position:"fixed", zIndex:100}} xs={24} sm={24} md={6} lg={4} xl={4}>
          <div style={{ width: "100%", height: "100vh" }}>
            <Menu
              style={{margin: 0}}
              defaultSelectedKeys={['1']}
              defaultOpenKeys={['sub1']}
              mode="inline"
              theme="dark"
              inlineCollapsed={collapsed}
            >
              <div style={{height:"50px", padding:"24px 35px 0 0", display:"flex", justifyContent: "flex-end"}} onClick={toggleCollapsed}>
                <a style={{color:"rgba(255, 255, 255, 0.65)"}}>
                  {collapsed?<Icon type="menu-unfold" />:<Icon type="menu-fold" />}
                </a>
              </div>
              <Menu.Item key="1" onClick={()=>props.setPage(0)}>
                <Icon type="pie-chart" />
                <span></span>
              </Menu.Item>
              <Menu.Item key="2" onClick={()=>props.setPage(1)}>
                <Icon type="desktop" />
                <span></span>
              </Menu.Item>
              <Menu.Item key="3" onClick={()=>props.setPage(2)}>
                <Icon type="inbox" />
                <span></span>
              </Menu.Item>
            </Menu>
          </div>  
        </Col >
        <Col xs={{span:20,offset:4}} sm={{span:18,offset:6}} md={{span:18,offset:3}} lg={{span:20,offset:2}} xl={{span:20,offset:2}} style={{display:"flex",justifyContent:"center", alignItems:"center", height: "100vh"}}>{props.children}</Col>
      </Row>
      
    </Layout>
    
  </>
)}

export default LayoutPage
