import React from "react"
import TableC from "../content/TableC.json"
import TableB from "../content/TableB.json"
import { Row, Col } from 'antd';

const Page3 = (props) =>{ 
  console.log(props)
  const result = props.result()

  console.log(result)

  const tableB = TableB.RECORDS
  let sum = [0, 0, 0]
  let Q = [0, 0, 0]
  let P = [0, 0, 0]

  const list = result.map((item,key)=>{
    switch(item[0]){
      case "sta":
        sum[0]=sum[0]+parseInt(item[2])
        sum[2]=sum[2]+parseInt(item[2])
        break
      case "loc":
        sum[0]=sum[0]+parseInt(item[2])
        sum[2]=sum[2]+parseInt(item[2])
        break
      case "obj":
        sum[0]=sum[0]+parseInt(item[2])
        sum[2]=sum[2]+parseInt(item[2])
        break
      case "gra":
        sum[1]=sum[1]+parseInt(item[2])
        sum[2]=sum[2]+parseInt(item[2])
        break
      case "vis":
        sum[1]=sum[1]+parseInt(item[2])
        sum[2]=sum[2]+parseInt(item[2])
        break
      case "rex":
        sum[2]=sum[2]+parseInt(item[2])
        sum[0]=sum[0]+parseInt(item[2])
        break
          
    }
    const tableC = TableC.RECORDS
    console.log(item)
    const age = tableC.filter((col)=>{
      // console.log(col[item[0]])
      if(col[item[0]]=="-"){return false}
      const range = col[item[0]].split("-")
      // console.log(item[0],range)
      const rowValue = props.inputData[item[0]]

      return (range[0].indexOf(">")==-1 && range.length==2 && parseInt(range[0])<=rowValue && parseInt(range[1])>=rowValue) || (range[0].indexOf(">")==-1 && range.length==1 && parseInt(range[0])==rowValue) || (range[0].indexOf(">")!=-1 && rowValue>=range[0].split(">")[1])
    })
    // console.log("age:",age[0]["age"])

    return (<tr key={key}>
      <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{item[0]}</td>
      <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{parseInt(age[0]["age"])-1}</td>
      <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{item[1]}</td>
      <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{item[2]}</td>
    </tr>)
  })

  console.log(tableB)
  sum.forEach((value,key)=>{
    let entry

    if(key==0){
      entry = tableB.filter((entry)=>{
        if(entry["gross"]=="-"){return false}
        const range = entry["gross"].split("-")
        // console.log(item[0],range)
        return (range.length==2 && range[0]<=value && range[1]>=value) || (range.length==1 && range[0]==value) || (range[0].indexOf(">")!=-1 && value>=range[0].split(">")[1])
      })
    }else if(key==1){
      entry = tableB.filter((entry)=>{
        if(entry["fine"]=="-"){return false}
        const range = entry["fine"].split("-")
        // console.log(item[0],range)
        return (range.length==2 && range[0]<=value && range[1]>=value) || (range.length==1 && range[0]==value) || (range[0].indexOf(">")!=-1 && value>=range[0].split(">")[1])
      })
    }else if(key==2){
      entry = tableB.filter((entry)=>{
        if(entry["total"]=="-"){return false}
        const range = entry["total"].split("-")
        // console.log(item[0],range)
        return (range.length==2 && range[0]<=value && range[1]>=value) || (range.length==1 && range[0]==value) || (range[0].indexOf(">")!=-1 && value>=range[0].split(">")[1])
      })
    }
    Q[key]=entry[0]["quotient"]
    P[key]=entry[0]["percentile"]
  })

  const MQ = (
    <>  
      <tr>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>SUM</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{sum[0]}</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{sum[1]}</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{sum[2]}</td>
      </tr>
      <tr>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>Q</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{Q[0]}</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{Q[1]}</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{Q[2]}</td>
      </tr>
      <tr>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>P</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{P[0]}</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{P[1]}</td>
        <td style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>{P[2]}</td>
      </tr>
    </>
  )
  // console.log(result)
  return (
  <Row>
    <Col>
      <table style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}>
        <thead>
          <tr>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h2></th>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>Age</h2></th>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>Percent</h2></th>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>Standard</h2></th>
          </tr>
        </thead>
        <tbody>
          {list}
        </tbody>
      </table>
    </Col>
    <Col>
      <table style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center",margin:"20px 0 0"}}>
        <thead>
          <tr>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</h2></th>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>GMQ</h2></th>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>FMQ</h2></th>
            <th style={{border:"2px solid #444444",background:"white",padding:"2px 2px",textAlign:"center"}}><h2>TMQ</h2></th>
          </tr>
        </thead>
        <tbody>
          {MQ}
        </tbody>
      </table>
    </Col>
  </Row>
)}

export default Page3
