import React from "react"
import { List, Input, Button } from 'antd';
import { Row, Col } from 'antd';


const Page2 = (props) => {
  console.log(props)
  const inputData = props.inputData 
  const setData = props.setData
  const inputHandler = (field, e)=>{
    let obj = {...inputData}
    obj[field]=e.target.value
    setData(obj)
  }


  const data = [
    <><span style={{width:"150px", fontWeight:600}}>Reflexes:</span><Input value={inputData.rex} onChange={(e)=>inputHandler("rex",e)} disabled={props.isAble} style={{width:"200px"}} placeholder="input"/></>,
    <><span style={{width:"150px", fontWeight:600}}>Stationary:</span><Input value={inputData.sta} onChange={(e)=>inputHandler("sta",e)} style={{width:"200px"}} placeholder="input"/></>,
    <><span style={{width:"150px", fontWeight:600}}>Locomotion:</span><Input value={inputData.loc} onChange={(e)=>inputHandler("loc",e)} style={{width:"200px"}} placeholder="input"/></>,
    <><span style={{width:"150px", fontWeight:600}}>Object Manipulation:</span><Input value={inputData.obj} onChange={(e)=>inputHandler("obj",e)} disabled={!props.isAble} style={{width:"200px"}} placeholder="input"/></>,
    <><span style={{width:"150px", fontWeight:600}}>Grasping:</span><Input value={inputData.gra} onChange={(e)=>inputHandler("gra",e)} style={{width:"200px"}} placeholder="input"/></>,
    <><span style={{width:"150px", fontWeight:600}}>VMI:</span><Input value={inputData.vis} onChange={(e)=>inputHandler("vis",e)} style={{width:"200px"}} placeholder="input"/></>
  ];
  return (
  <>
    <div>
      <List style={{background:"white"}}
        header={<h1>Row Data</h1>}
        bordered
        dataSource={data}
        renderItem={item => (
          <List.Item>
            {item}
          </List.Item>
        )}
      />
      <Button style={{display:"block", margin:"20px auto 0 auto"}} type="primary" onClick={()=>{setData({...inputData, isInputSet:true})}}></Button>
    </div>
  </>
)}

export default Page2
