import React, {useState} from "react"
import monthData from "../content/month.json"
import TableB from "../content/TableB.json"
import LayoutPage from "../components/layout"
import Page1 from "../components/page-1"
import Page2 from "../components/page-2"
import Page3 from "../components/page-3"
import "../style/index.css"


const IndexPage = () => {
  
  //page1
  const [dateDate, setDate] = useState({
    date1: 0,
    date2: 0,
    month: 0,
    day: 0,
    isDateSet: false,
    msg: ""
  })



  const [page, setPage] = useState(0);
  const [month, setMonth] = useState(22);
  const [inputData, setData] = useState({
    rex:"",
    sta:"",
    loc:"",
    obj:"",
    gra:"",
    vis:"",
    isInputSet: false
  })
  // const inputData = {
  //   rex:12,
  //   sta:34,
  //   loc:94,
  //   obj:0,
  //   gra:39,
  //   vis:80
  // }
  const getMonthEntry = (month)=>{
    return monthData.RECORDS.filter(function(item){
      return item.month_max>=month && item.month_max<=month
    })
  }
  const table = getMonthEntry(month)
  const getPercentScore = (field, value, table)=>{
    const col = table.filter(function(item){
      const range = item[field].split("-")
      if(item[field]=="-"){return false}
 
      return (range.length==2 && parseInt(range[0])<=value && parseInt(range[1])>=value) || (range.length==1 && parseInt(range[0])==value)
    })
    return [field, col[0].percentile_rank, col[0].score]
  }

  


  const resultList = ()=>{

    const FilterData = Object.keys(inputData).filter((field) => {
      return inputData[field]!="" && field!="isInputSet"
    })
    const percentScoreData = FilterData.map(field => {
      return getPercentScore(field, inputData[field], table)
    });
  
    return percentScoreData
  }
    console.log(table)
  const menu = [<Page1 setMonth={setMonth} dateDate={dateDate} setDate={setDate}/>, <Page2 inputData={inputData} setData={setData} isAble={month>=12}/>, <Page3 inputData={inputData} result={resultList}/>]  
  
  return (
  <>
    <LayoutPage setPage={setPage}>{menu[page]}</LayoutPage>
    
  </>
)}

export default IndexPage
