import React, {useState} from "react"
import { DatePicker } from 'antd';
import { Select, Button } from 'antd';

const { Option } = Select;

const Page1 = (props) => {
  const dateDate = props.dateDate
  const setDate = props.setDate
  const onChangeTest = (date)=>{
    setDate({...dateDate, date1:date})
  }
  const onChangeBirth = (date)=>{
    setDate({...dateDate, date2:date})
  }
  const onChangeMonth = (value)=>{
    console.log(value)
    setDate({...dateDate, month:value})
  }
  const onChangeDay = (value)=>{
    console.log(value)
    setDate({...dateDate, day:value})
  }
  const setMonth = ()=>{
    let days = dateDate.date1.diff(dateDate.date2, "days")-(dateDate.month*30+dateDate.day)
    let months = Math.floor(days/30)
    console.log(months)
    if(months>0){
      props.setMonth(months)
      setDate({...dateDate, isDateSet:true, msg:`Total month: ${months}`})
    }else{
      setDate({...dateDate, isDateSet:false, msg:""})
    }
  }


  return (<>
    <div>
      <h1></h1>
      <span style={{display:"inline-block",margin:"0 4px 20px"}}> : </span><DatePicker onChange={onChangeTest} defaultValue={dateDate.date1==0?undefined:dateDate.date1}/>
      <br />
      <span style={{display:"inline-block",margin:"0 4px 20px"}}> : </span><DatePicker onChange={onChangeBirth} defaultValue={dateDate.date2==0?undefined:dateDate.date2}/>
      <br />
      <span style={{display:"inline-block",margin:"0 4px 20px"}}> : </span><Select defaultValue={dateDate.month==0?"":dateDate.month} style={{ width: 120 }} onChange={onChangeMonth}>
        {[...Array(13).keys()].map((key)=><Option value={key} key={key}>{key}</Option>)}
        
      </Select>
      <Select defaultValue={dateDate.day==0?"":dateDate.day} style={{ width: 120 }} onChange={onChangeDay}>
        {[...Array(31).keys()].map((key)=><Option value={key} key={key}>{key}</Option>)}
      </Select>
      <br/>
      <Button type="primary" onClick={setMonth}></Button><span style={{color:dateDate.isDateSet?"green":"red", marginLeft:"20px"}}>{dateDate.msg}</span>
    </div>
  </>)
}

export default Page1
